.postbox.acf-postbox {
    @include make-container();

    padding: 0;
    > .inside.acf-fields {
        @include make-row();
    }
}
.acf-table>.acf-field-settings>tr{
    display: table-row !important;
    >td{
        display: table-cell !important;
    }
}
.acf-fields {
    @include make-row;
    &.-left {
        flex-direction: column;
        flex-wrap: none;
        > .acf-field {
            width: 100%;
            flex-direction: row;
            max-width: none;
            padding-top: $grid-gutter-width / 2;
            padding-bottom: $grid-gutter-width / 2;
            > .acf-label {
                @include make-col(3);

                float: none;
                padding: $grid-gutter-width / 2;
            }
            > .acf-input {
                @include make-col(9);

                padding: $grid-gutter-width / 2;
            }
        }
        label {
            margin-bottom: 0;
        }
    }
}

.acf-field {
    @include make-col-ready;

    display: flex;
    flex-direction: column;
    float: none !important;
    &::before,
    &::after {
        display: none !important;
    }
}

.acf-input {
    display: flex;
    align-items: stretch;
    .acf-input-prepend {
        order: 1;
        height: auto;
    }
    .acf-input-append {
        order: 3;
        height: auto;
    }
    .acf-input-wrap {
        order: 2;
        display: flex;
        flex: 1 1 auto;
        > input {
            flex: 1 0 100%;
            height: 100%;
            width: 100%;
        }
    }
}
